.modal {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContent {
    padding: 20px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: lightskyblue;
}